import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import ContatoView from '../views/ContatoView'
import ServicoView from '../views/ServicoView'
import GaleriaView from '../views/GaleriaView'
const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/Contato',
    name: 'Contato',
    component: ContatoView
  },
 {
  path: '/Servico',
  name: 'Servico',
  component: ServicoView
},
{
  path: '/Galeria',
  name: 'Galeria',
  component: GaleriaView
}

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
